












import Catalog from '@/views/file-list/components/catalog/index.vue';

import {
  computed,
  defineComponent,
} from '@vue/composition-api';
import axios from '@/utils/axios';
import useLoader from '@4dst-saas/public-utils/dist/composables/use-loader';
import CatalogModel, { SearchParams, CatalogOptions } from '@/models/Catalog';
import { getContextUser } from '@/loaders/context';
import i18n from '@/i18n';
import { Meeting } from '../typings';

const getMeetingId = async () => {
  // 防止页面props没有meetingId:比如会议库刷新后拿不到MeetingId
  // meetingId统一在这里获取,后续meetingId使用meetingInfo.id
  return (await axios({
    url: '/meeting/meeting/id',
  })).data;
};


export default defineComponent({
  name: 'up-meeting',
  components: { Catalog },
  props: {
    meetingId: {
      type: String,
      // required: true,
    },
    id: String,
    entryAppType: Number,
    searchParams: String,
  },
  setup(props, { root: { $router } }) {
    const computedSearchParams = computed(
      () => {
        if (!props.searchParams) return null;
        return JSON.parse(props.searchParams) as SearchParams;
      },
    );
    const user = computed(() => getContextUser());
    const userTitle = computed(() => user.value?.name || user.value?.account || '');
    const rootCatalogName = computed(() => i18n.t('virtual-meetup.public.store_root_catalog', [userTitle.value]));
    const meeting = useLoader(async ({ meetingId }) => {
      meetingId ??= await getMeetingId();
      if (!meetingId) return {};// 可能有页面不需要meetingId 比如会议库,没传meetingId也不阻塞线程
      return (await axios.get<Meeting>('meeting/meeting/info', { params: { meetingId } })).data;
    }, props);
    const search = async (_searchParams?: SearchParams, activeModule?: CatalogOptions) => {
      // KHD-1086
      if (!activeModule) return undefined;
      await $router.replace({
        query: {
          id: activeModule.id,
          meetingId: props.meetingId,
          entryAppType: `${props.entryAppType}`,
          ...(_searchParams ? { searchParams: JSON.stringify(_searchParams) } : {}),
        },
      });
      return undefined;
    };
    return {
      meeting,
      search,
      computedSearchParams,
      rootCatalogName,
      enterFolder(catalog: CatalogModel) {
        $router.push({
          query: {
            ...$router.currentRoute.query,
            searchParams: 'null',
            id: catalog.id,
            meetingId: props.meetingId,
            entryAppType: `${catalog.entryAppType}`,
          },
        });
      },
    };
  },
});
